<template>
  <div class="home">
    <!--<img src="./../assets/spring-boot-vuejs-logo.png">-->
    <h1>{{ msg }}</h1>
    <h3>This site contains more stuff :)</h3>
    <ul>
      <li>HowTo call REST-Services:</li>
      <li><a href="/#/callservice/" target="_blank">/callservice</a></li>
      <li>HowTo to play around with Bootstrap UI components:</li>
      <li><a href="/#/bootstrap/" target="_blank">/bootstrap</a></li>
      <kanban-board :stages="statuses" :blocks="blocks" @update-block="updateBlock">
        <div v-for="item in blocks" :slot="item.id" :key="item.id">
            <div>
                <strong>id:</strong> {{ item.id }}
            </div>
            <div>
                {{ item.title }}
            </div>
        </div>
      </kanban-board>
    </ul>
  </div>
</template>

<script>
import faker from 'faker'
import { debounce } from 'lodash'

export default {
  name: 'home',
  data () {
    return {
      msg: 'Welcome to your Vue.js powered Spring Boot App',
      statuses: ['on-hold', 'in-progress', 'needs-review', 'approved', 'test'],
      blocks: []
    }
  },
  mounted () {
    for (let i = 0; i <= 10; i += 1) {
      this.blocks.push({
        id: i,
        status: this.statuses[Math.floor(Math.random() * 4)],
        title: faker.company.bs()
      })
    }
  },
  methods: {
    updateBlock: debounce(function (id, status) {
      this.blocks.find(b => b.id === Number(id)).status = status
    }, 500)
  }
}
</script>

<style lang="scss">
  @import 'node_modules/vue-kanban/src/assets/kanban.scss';

  $on-hold: #FB7D44;
    $in-progress: #2A92BF;
    $needs-review: #F4CE46;
    $approved: #00B961;
    * {
    	box-sizing: border-box;
    }
    body {
    	background: #33363D;
    	color: white;
    	font-family: 'Lato';
    	font-weight: 300;
    	line-height: 1.5;
    	-webkit-font-smoothing: antialiased;
    }
    .drag-column {
        &-on-hold {
            .drag-column-header,
            .is-moved,
            .drag-options {
                background: $on-hold;
            }
        }
        &-in-progress {
            .drag-column-header,
            .is-moved,
            .drag-options {
                background: $in-progress;
            }
        }
        &-needs-review {
            .drag-column-header,
            .is-moved,
            .drag-options{
                background: $needs-review;
            }
        }
        &-approved {
            .drag-column-header,
            .is-moved,
            .drag-options {
                background: $approved;
            }
        }
    }
    .section {
    	padding: 20px;
    	text-align: center;
    	a {
    		color: white;
    		text-decoration: none;
    		font-weight: 300;
    	}
    	h4 {
    		font-weight: 400;
    		a {
    			font-weight: 600;
    		}
    	}
    }
</style>



